footer{
    .container{
        color: white;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        &__social-media{
            padding: 0px;
            img{
                border-radius: 50%;
                padding-bottom: 0px;
                width: 30px;
            }
        }
        
        &__established{
            font-size: 16px;
            color: #232227;
            padding-top: 10px;
            text-transform: lowercase;
        }
            a{
                padding: 2px;
            }
        
    }
}



